import "../App.css";
import { Column } from "@antv/g2plot";
import { useEffect } from "react";
import { Parallax } from "rc-scroll-anim";

const data = [
  {
    Type: "Ëû∫Ëõ≥Á≤â",
    Count: 8,
  },
  {
    Type: "ÁÉ§ËÇâ",
    Count: 7,
  },
  {
    Type: "Èù¢Êù°",
    Count: 5,
  },
  {
    Type: "Á±≥Á∫ø",
    Count: 5,
  },
  {
    Type: "ÁÉ§‰∏≤",
    Count: 4,
  },
];

const colors = ["#E98102", "#BEB56D", "#1BBC6B"];

function Foodcolumn() {
  useEffect(() => {
    const columnPlot = new Column("lineCon", {
      data,
      xField: "Type",
      yField: "Count",
      label: {
        position: "middle",
      },
      xAxis: {
        label: {
          autoHide: true,
          autoRotate: false,
        },
      },
      color: ({ Type }) => {
        if (Type === "Ëû∫Ëõ≥Á≤â") {
          return colors[0];
        }
        return colors[1];
      },
      animation: {
        // ÈÖçÁΩÆÂõæË°®Á¨¨‰∏ÄÊ¨°Âä†ËΩΩÊó∂ÁöÑÂÖ•Âú∫Âä®Áîª
        appear: {
          animation: "scale-in-y", // Âä®ÁîªÊïàÊûú
          duration: 5000, // Âä®ÁîªÊâßË°åÊó∂Èó¥
          delay: 500,
          easing: "easeCircleOut",
        },
      },
    });
    columnPlot.render();
  });
  return (
    <Parallax
      className="up"
      animation={{ y: 0, opacity: 1, playScale: [0.6, 0.8] }}
    >
      <div id="lineCon" style={{ width: 300, height: 250, margin: 16 }} />
    </Parallax>
  );
}

export default Foodcolumn;
